*
{
  margin: 0;
  padding: 0;
  font-family: "verdana", cursive, sans-serif;
  /* color: #ffff; */
}
body{
  /*background-image: url(2.webp);*/
  /* background-repeat: no-repeat; */
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.header
{
  font-weight: bold;
  text-shadow: 0 0 3px #f7f4f4, 0 0 5px #f2f2f5;
  color: blueviolet;
  text-align: center;
}
.app
{
 
  background-size: cover;
  /* background-color: linear-gradient(180deg,#f30dcc 0%, #ff8100 80%); */
  /* background-color: rgb(23, 141, 141); */
  width: 95%;
  height: 200px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
} 
.score-section{
  color: skyblue;
  font-size: 45px;
}
.question-Section
{
  position: relative;
  font-size: 30px;
  color: white;
}
.question-count{
  margin-bottom: 20px;
}
.question-count span{
  color: crimson;
  font-weight: bold;
  text-shadow: 0 0 3px #fcf8f8, 0 0 5px #f6f6fa;
  font-size: 20px;
}
.question-text
{
  font-weight: bold;
  text-shadow: 0 0 3px #f8f5f5, 0 0 5px #f3f3f5;
  margin-bottom: 12px;
  color: blue;
}

.answer-section{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button{
  width: 100%;
  font-size: 16px;
  /* color: #fff; */
  background-color: linear-gradient(180deg,#f30dcc  0%, #ff8100 80%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}

button:hover{
  background-color: #1b295c;
}



